import React, { useRef, useCallback, useState, useEffect } from "react";
import Dialog from "@mui/material/Dialog";
import HTMLFlipBook from "react-pageflip";
import "./style.scss"; // Update to your actual style file
import { CustomButton } from "component/common";
import { KeyboardArrowLeftOutlined } from "@mui/icons-material";
import { eCatalogueProducts } from "config";
import pageTurnSound from "assets/audio/page-flip.mp3";

const Page = React.forwardRef((props, ref) => {
  return (
    <div className="page-container" ref={ref}>
      {props.children}
    </div>
  );
});

const ECatalogueBook = () => {
  const book = useRef();
  const audioRef = useRef();
  const [currentPage, setCurrentPage] = useState(0);

  const playPageTurnSound = () => {
    if (audioRef.current) {
      audioRef.current.currentTime = 0;
      audioRef.current.play();
    }
  };

  const onFlip = useCallback(
    (e) => {
      const currentPageNumber = e.data;

      if (currentPageNumber !== currentPage) {
        playPageTurnSound();
        setCurrentPage(currentPageNumber);
      }
    },
    [currentPage]
  );

  useEffect(() => {
    audioRef.current.load();

    return () => {
      if (audioRef.current) {
        audioRef.current.pause();
      }
    };
  }, []);

  return (
    <>
      <audio ref={audioRef} src={pageTurnSound} preload="auto" />
      <div className="responsive-flipbook-container">
        <HTMLFlipBook
          className="HTMLFlipBook"
          showCover={true}
          onFlip={onFlip}
          ref={book}
          width={600}
          height={500}
          onInit={({ object }) => {
            console.log(object.events);
          }}
        >
          {eCatalogueProducts.map(({ id, page_no, image }) => (
            <Page
              key={id}
              number={page_no}
              getPageCount={(c) => console.log(c, "count")}
            >
              <img
                height="100%"
                width="100%"
                src={image}
                alt={`img-${page_no}`}
              />
            </Page>
          ))}
        </HTMLFlipBook>
      </div>
    </>
  );
};

export const ECatalogue = ({ isOpen, setOpenCatalogue }) => {
  return (
    <div>
      <Dialog className="dialog-container" open={isOpen}>
        <div className="d-flex align-items-center row">
          <div className="col-5">
            <CustomButton
              startIcon={<KeyboardArrowLeftOutlined />}
              variant="contained"
              className="back-to-website p-3"
              onClick={() => setOpenCatalogue(false)}
            >
              <label className="back-to-website-label cursor-pointer">
                Back to Website
              </label>
            </CustomButton>
          </div>
          <div className="col-6 label-container">
            <label className="label-wall">Wall</label>
            <label>antics</label>
          </div>
          <div className="d-flex justify-content-center pt-5 e-catalogue-container">
            <ECatalogueBook />
          </div>
        </div>
      </Dialog>
    </div>
  );
};
